<template>
	<div  class="CheckLoad_check">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>-</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>				
				<span></span>
				<input  ref="OrderKey"  v-model="OrderKey" readonly></input>													
			</div>
			<div>				
				<span></span>
				<input   ref="CaseNo"  v-model="CaseNo" readonly></input>			
			</div>
			<div class="qty">				
				<span></span>
				<i v-if="Qty"  @click="clearInput('Qty')" class="iconfont icon-cuowu"></i>
				<input ref="Qty"  @focus="g_focus('Qty')"   @keyup.enter="toSku"  v-model="Qty" ></input>
				<input  ref="Susr2"  v-model="Susr2" readonly></input>
			</div>
			<div>				
				<span></span>
				<input  placeholder=""  @focus="g_focus('Sku')"   @keyup.enter="checkSku"   ref="Sku"  v-model="Sku" ></input>
				<i v-if="Sku" @click="clearInput('Sku')" class="iconfont icon-cuowu"></i>									
			</div>
			<div class="goods_name">
				<span></span>
				<textarea v-model="Descr" ref="Descr"  readonly></textarea>
			</div>
			<div class="checkQty">				
				<span></span>
				<input ref="CheckedQty"  v-model="CheckedQty"  readonly></input>
				<span class="daiCheck"></span>
				<input  ref="PreCheckQty"  v-model="PreCheckQty" readonly></input>
			</div>	
			<div>				
				<span></span>
				<input  ref="SumPreCheckQty"  v-model="SumPreCheckQty" readonly></input>													
			</div>	
			<div class="g_doubleBtn_chckOrder">				
				<div @click="check"></div>
				<div @click="closeCase"></div>	
				<div @click="checkSku"></div>
			</div>
		</div>
		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span></span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'></p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"CheckLoad_check",
		data(){
			return{
				OrderKey:'',
				CaseNo:'',
				Qty:'1',
				Susr2:'',//
				Sku:'',
				Descr:'',//
				CheckedQty:'',//
				SumPreCheckQty:'',//				
				SumQty:'',// 
				StorerKey:'',
				tipsHide:false,//
				msg:"",//
				inputFocus:"",		
//				CheckOrder:"",
				BoxType:'ByLoad',
				SumCheckedQty:'',//
			}
		},
		computed:{
			PreCheckQty:function(){
				return this.SumQty-this.CheckedQty;
			}
		},
		mounted() {
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
			this.$refs['Sku'].focus();
		},
		created(){
			var that = this;
			// SO
			if(this.$route.query.OrderKey){
				that.OrderKey = that.$route.query.OrderKey;				
			}
			// 
			if(this.$route.query.CaseNo){
				that.CaseNo = that.$route.query.CaseNo;				
			}
			
			// 
			if(this.$route.query.StorerKey){
				that.StorerKey = that.$route.query.StorerKey;				
			}
			
		},
		methods:{
			// iOS/android
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				if(inputName == "Qty") {

					this.getFocus('Sku');
					
				}else if(inputName == "Sku") {

					this.checkSku();
				}
			},
			//  
			toIndex:function(){
				this.$router.push({
					name:"CheckLoad",
					query:{
						OrderKey : this.OrderKey,
						CaseNo: this.CaseNo,
					}
				})
			},	
			toSku:function(){
				this.$refs['Sku'].focus();
			},
			//  
			checkSku:function(){
				var that = this;
			  if(this.Qty =='') {
 				that.msg = "";
				that.tipsShow("Qty");
				that.inputFocus = "Qty";
				return false;
 			     }
			   if(this.Sku =='') {
 				that.msg = "";
				that.tipsShow("Sku");
				that.inputFocus = "Sku";
				return false;
 			     }
				var currentUrl = this.HOST + "RFCheckSku.do";
				var postData = {
					OrderKey : that.OrderKey,
					 BoxType  : that.BoxType,
					      Sku : that.Sku,
					      Qty : that.Qty,
					   CaseNo : that.CaseNo,
					StorerKey : that.StorerKey,
				}
				this.httpRequest(currentUrl,postData,function(res){
					if(res.data.data.length){
//						that.$store.state.SkuBound_SelectSkuData = res.data.data;
						that.Descr=res.data.data[0].Descr;
						that.CheckedQty=res.data.data[0].SumCheckedQty;
						that.SumQty=res.data.data[0].SumQty;
						that.SumPreCheckQty=res.data.data[0].SumPreCheckQty;
						that.Susr2=res.data.data[0].Susr2;
						that.Sku='';
						that.$refs['Sku'].focus();
					}else{

						that.tipsShow("Sku");

						that.msg = "";

						that.inputFocus = "Sku";
					}
				},"Sku")
			},
			//   
			check:function(){

				var that = this;

				var currentUrl = this.HOST + "RFCheck.do";

				var postData = {
					OrderKey : that.OrderKey,
					BoxType:that.BoxType,
				}

				this.httpRequest(currentUrl,postData,function(res){

					if(res.data.data.length){
						var sumQty=0;					
						for(var i=0;i<res.data.data.length;i++){
							sumQty=sumQty+res.data.data[i].SumQty;
						}											
                       if(sumQty - res.data.data[0].SumCheckedQty ==0){
                       	that.$router.push({
						name:"CheckLoad",
						})                 	
                       }else{
                       	that.tipsShow("");
                       	that.msg = ","+(sumQty - res.data.data[0].SumCheckedQty);
						that.inputFocus = "Sku";
                       }
                       
					}else{

						that.tipsShow("");

						that.msg = "";

						that.inputFocus = "Orderkey";
					}
				},"OrderKey")
			},
			//
			closeCase:function(){
				var that = this;
				var currentUrl = this.HOST + "RFCheckSku.do";
				var postData = {
					OrderKey : that.OrderKey,
					 BoxType  : that.BoxType,
					      Sku : that.Sku,
					      Qty : that.Qty,
					   CaseNo : that.CaseNo,
					StorerKey : that.StorerKey,
				}				
				if(this.Qty !='' && this.Sku !=''){
				this.httpRequest(currentUrl,postData,function(res){
					if(res.data.data.length){
						that.Descr=res.data.data[0].Descr;
						that.CheckedQty=res.data.data[0].SumCheckedQty;
						that.SumQty=res.data.data[0].SumQty;
						that.SumPreCheckQty=res.data.data[0].SumPreCheckQty;
						that.Susr2=res.data.data[0].Susr2;
					 	that.$router.push({
						name:"CheckLoad",
						query:{
								OrderKey : that.OrderKey,
								       CaseNo: that.CaseNo,
							}
						})					
					
					}else{

						that.tipsShow("Sku");

						that.msg = "";

						that.inputFocus = "Sku";
					}
				},"Sku")	
				}else{
					this.toIndex();
				}				
			}
			
		}
	}
</script>
<style scoped lang="less">
.CheckLoad_check{
	&>.container{
		margin-top: 110/50rem;
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}
		&>div{
			width:100%;
			height: 80/50rem;
			margin-bottom: 20/50rem;
			box-sizing: border-box;
			position: relative;
			overflow: hidden;
			span{
				width: 160/50rem;
				text-align: right;
				float: left;
				height:65/50rem;
				line-height: 65/50rem;
			}
			input{
				margin-left: 10/50rem;
				width:480/50rem;
				height:60/50rem;
				float: left;
				padding:0 10/50rem;
			}
			input[readonly]{
				background-color: #f0f0f0;
				outline: none;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 35/50rem;
				top: 15/50rem;
			}
		}
		&>div.qty{
			padding: 0;
			span{
				padding-right: 5/50rem;
				text-align: right;
			}
			input{
				width:224/50rem;
				margin-left: 5/50rem;
			}
			i{
				left:370/50rem;
				top: 15/50rem;
			}
		}
		&>div.checkQty{
			padding: 0;
			span{
				padding-right: 5/50rem;
				text-align: right;
				float: left;
			}	
			input{
				width:162/50rem;
				margin-left: 5/50rem;
				float: left;
			}
			&>.daiCheck{
				width: 120/50rem;
			}
		}
		&>div.goods_name{
			height: 110/50rem;
			textarea{
				margin-left: 10/50rem;
				width:480/50rem;
				height:100/50rem;
				float: left;
				padding:0 10/50rem;
				background-color: #f0f0f0;
				outline: none;
			}
		}
		&>div.g_doubleBtn_chckOrder{
			margin-bottom: 50/50rem;
            width:100%;
            display: flex;
            height:65/50rem;
            padding:0 20/50rem;
            margin-top: 30/50rem;
	        &>div{
	        	flex-grow:1;
		        height:65/50rem;
		        line-height: 65/50rem;
		        color: #fff;
		        background-color: #285DCE;
		        text-align: center;
		        margin-left: 30/50rem;
		        border-radius: 5/50rem;
			}
			&>div:nth-child(1){
				margin-left: 0/50rem;
			}
		}
	}
}

@media screen and (max-width: 330px){
	.CheckLoad_check{
		&>.container{
			margin-top: 100/50rem;
			&>div{
				height: 75/50rem;
				margin-bottom: 0;
				i{
					right:90/50rem;
					font-size: 40/50rem;
					top: 15/50rem;
				}
			}
			&>div.goods_name{
				height: 105/50rem;
				textarea{
					height:90/50rem;
				}
			}
			&>div.g_doubleBtn_chckOrder{
	            margin-top: 10/50rem;
			}
		}
	}
}
@media screen and (max-width: 277px){
	.CheckLoad_check{
		&>.container{
			&>div{
				height: 80/50rem;
				margin-bottom: 20/50rem;
				span{
					width: 200/50rem;
					height:75/50rem;
					line-height: 75/50rem;
				}
				input{
					height:75/50rem;
				}
				i{
					right:70/50rem;
					font-size: 45/50rem;
					top: 16/50rem;
				}
			}
			&>div.qty{
				i{
					left:396/50rem;
					top: 15/50rem;
				}
			}
			&>div.checkQty{
				input{
					width:147/50rem;
				}
				&>.daiCheck{
					width: 150/50rem;
				}
			}
			&>div.goods_name{
				textarea{
					height:100/50rem;
				}
			}
			&>div.g_doubleBtn_chckOrder{
				margin-top: 40/50rem;
	            height:80/50rem;
		        &>div{
			        height:80/50rem;
			        line-height: 80/50rem;
				}
			}
		}
	}
}
</style>


