<template>
	<div class="CommodityInfoEntry">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span></span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<div>
					<span></span>
					<input  class="readonly_input" readonly  v-model="CommodityCode"></input>
				</div>
				<div>
					<span></span>
					<input  class="readonly_input"  readonly  v-model="CustomerCode"></input>
				</div>
			</div>
			<div class="goodsName">
				<span></span>
				<textarea  class="readonly_input" v-model="CommodityName"   readonly></textarea>
			</div>
			<div>
				<span></span>
				<input ref="StandardWeight" @keyup.enter="getFocus('StandardVolume')" v-model="StandardWeight"    @focus="g_focus('StandardWeight')"></input>
				<span>g</span>
				<i v-if="StandardWeight" @click="clearInput('StandardWeight')"  class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span></span>
				<input  ref="StandardVolume"   @keyup.enter="getFocus('LengthWidthHeight')" v-model="StandardVolume"   @focus="g_focus('StandardVolume')"></input>
				<span>cm</span>
				<i v-if="StandardVolume" @click="clearInput('StandardVolume')"  class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>**</span>
				<input ref="LengthWidthHeight"  @keyup.enter="submit" v-model="LengthWidthHeight"   @focus="g_focus('LengthWidthHeight')"></input>
				<span>cm</span>
				<i v-if="LengthWidthHeight" @click="clearInput('LengthWidthHeight')"  class="iconfont icon-cuowu"></i>
			</div>
			<p class="positionFixed"><span class="g_singleBtn" @click="submit"></span></p>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  class="g_tips">
			<div>
				<span></span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'></p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"",
		data(){
			return{
				tipsHide:false,//
				msg:"",//
				CommodityCode:"",//
				CustomerCode:"",//
				CommodityName:"",//
				StandardWeight:"",//
				StandardVolume:"",//
				LengthWidthHeight:"",//**
			}
		},
		created(){

			this.CommodityCode = this.$route.query.sku;//
			this.CustomerCode = this.$route.query.storerkey;//
			this.CommodityName = this.$route.query.descr;//
			this.StandardWeight = this.$route.query.stdcube;//
			this.StandardVolume = this.$route.query.stdgrosswgt;//
			this.LengthWidthHeight = this.$route.query.susr10;//**
		},
		mounted() {
			this.$refs['StandardWeight'].select();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		},
		methods:{
			// 
			goPrev:function(){

				var that = this;

				this.$router.push({
					name:"ScanCommodityCode",
					query:{
						input_CommodityCode: that.CommodityCode
					}
				})
			},
			// iOS/android
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				
				this[inputName] = qrcode;
				
				if(inputName == "StandardWeight") {

					this.getSelect('StandardVolume');

				}else if(inputName == "StandardVolume") {

					this.getSelect('input_LibraryCode');

				}else if(inputName == "LengthWidthHeight") {

					this.submit();
				}
				
			},
			//  
			submit(){

				var  that = this;

				if(!this.StandardWeight){
					
					that.msg = "";

					that.inputFocus = "StandardWeight";

					that.tipsShow("StandardWeight");

					return;
				}else if(!this.StandardVolume){
					
					that.msg = "";

					that.inputFocus = "StandardVolume";

					that.tipsShow("StandardVolume");

					return;
				}else if(!this.LengthWidthHeight){
					
					that.msg = "**";

					that.inputFocus = "LengthWidthHeight";

					that.tipsShow("LengthWidthHeight");

					return;
				}

				// 
				var SkuInfoUrl = that.HOST + "RFReceiveSkuInfo.do";

				var SkuInfo_Data = {
					storerkey : that.CustomerCode,//
					whseid : that.$store.state.WarehouseCode,// code
					sku : that.CommodityCode,//
					stdgrosswgt : that.StandardVolume,//
					stdcube : that.StandardWeight, //
					susr10 : that.LengthWidthHeight,//**
				}

				that.httpRequest(SkuInfoUrl,SkuInfo_Data,function(result){

					if(result.data.data == "1"){

						// 
						var LottableUrl = that.HOST + "RFReceiptSkuLottable.do";

						var Lottable_Data = {
							ReceiptKey : that.$store.state.Receipt_SubmitData[0].receiptkey,//
							UserID : that.$store.state.UserID,//
							Sku : that.CommodityCode//
						}

						that.httpRequest(LottableUrl,Lottable_Data,function(resData){
					
							if(resData.data.data.length){
									
								that.$store.state.Receipt_PrevBatchProp = resData.data.data;
								
							}else{
								that.$store.state.Receipt_PrevBatchProp = [];
							}

							// RF
							var SummaryUrl = that.HOST + "RFReceiveSku.do";

							var sum_Data = {
								ReceiptKey : that.$store.state.Receipt_SubmitData[0].receiptkey,//
								StorerKey : that.$store.state.Receipt_SubmitData[0].storerkey,//
								Sku : that.CommodityCode//
							}

							that.httpRequest(SummaryUrl,sum_Data,function(res){

								console.log(res.data.data)

								if(res.data.data.length){
										
									that.$store.state.RFReceiveSkuData = res.data.data;

									that.$router.push({
										name:"TakeDelivery",
										query:{
											Sku : that.CommodityCode//
										}
									})
									
								}else{
									that.tipsShow();

									that.inputFocus = "CommodityCode";

									that.msg = "";
								}
							},'CommodityCode')
						})

					}else{
						that.tipsShow();

						that.inputFocus = "StandardWeight";

						that.msg = "";
					}
				},"StandardWeight")
			}
		}
	}
</script>
<style lang="less">
	.CommodityInfoEntry{
		.container{
			padding-top: 15/50rem;
			&>div{
				width:100%;
				height: 60/50rem;
				box-sizing: border-box;
				margin-bottom: 15/50rem;
				overflow: hidden;
				position: relative;
				&>div{
					width:375/50rem;
					float: left;
					span{
						width: 130/50rem;
						margin-left: 10/50rem;
						float: left;
						text-align: left;
					}
					input{
						width:220/50rem;
						float: left;
						height:55/50rem;
						padding-left: 5/50rem;
						box-sizing: border-box;
					}
				}
				.icon-cuowu{
					position:absolute;
					right:115/50rem;
					font-size: 36/50rem;
					top: 8/50rem;
				}
				&>span{
					width: 130/50rem;
					height: 55/50rem;
					line-height: 55/50rem;
					float: left;
					margin-left: 10/50rem;
					text-align: left;
				}
				&>span:nth-child(3){
					width:80/50rem;
				}
				&>input{
					width:520/50rem;
					height:55/50rem;
					float: left;
					padding: 0 10/50rem;
					box-sizing: border-box;
				}
			}
			&>.goodsName{
				height:170/50rem;
				margin-bottom: 15/50rem;
				overflow: hidden;
				&>span{
					width: 130/50rem;
					text-align: left;
					float: left;
				}
				&>textarea{
					width:595/50rem;
					height:170/50rem;
					float:left;
					outline:none;
					font-size: 29/50rem;
					box-sizing: border-box;
				}
			}
			.readonly_input{
				background-color: #F0F0F0;
				border:none;
			}
			.readonly_input:focus{
				outline: none;
			}
		}
	}

    @media screen and (max-width: 329px) and (min-width: 291px) {
   		.CommodityInfoEntry{
			.container{
				&>div{
					height: 80/50rem;
					line-height: 80/50rem;
					&>div{
						span{
							width: 150/50rem;
							height: 65/50rem;
							line-height: 65/50rem;
						}
						input{
							width: 200/50rem;
							height:65/50rem;
							padding:0 10/50rem;
						}
					}
					.icon-cuowu{
						font-size:40/50rem;
						top: -5/50rem;
					}
					&>span{
						width: 150/50rem;
						height: 65/50rem;
						line-height: 65/50rem;
					}
					input{
						height:65/50rem;
						width: 490/50rem;
					}
				}
				&>.goodsName{
					margin-bottom: 5/50rem;
					margin-top: 5/50rem;
					&>span{
						width: 150/50rem;
					}
					&>textarea{
						width: 575/50rem;
						padding:0 10/50rem;
						height: 150/50rem;
					}
				}
				p.positionFixed{
					height:85/50rem;
				}
			}
		}
    }
	
    @media screen and (max-width: 290px){
    	.CommodityInfoEntry{
			.container{
				font-size: 40/50rem;
				&>div{
					height: 80/50rem;
					&>div{
						span{
							width: auto;
							height: 75/50rem;
							line-height: 75/50rem;
							margin-left: 0;
						}
						input{
							width:200/50rem;
							height:75/50rem;
						}
					}
					.icon-cuowu{
						right: 130/50rem;
						font-size:50/50rem;
						top:10/50rem;
					}
					&>span{
						margin-left: 0;
						text-align: left;
						width:auto;
						height: 75/50rem;
						line-height: 75/50rem;
						margin-right: 10/50rem;
					}
					&>input{
						width:480/50rem;
						height:75/50rem;
					}
				}
				&>.goodsName{
					margin-bottom: 10/50rem;
					&>span{
						width:auto;
						margin-right: 0;
					}
					&>textarea{
						width: 575/50rem;
						font-size: 25/50rem;
						padding:0 10/50rem;
						margin-left: 0;
					}
				}
			}
		}
    }
</style>


